.general{height:100%}.general .item{display:flex;width:44%;background:rgba(40,176,255,.1);padding:12px;margin-bottom:12px;align-items:center;justify-content:space-between}.general .item .switch{width:0px;height:20px;position:relative;cursor:pointer}.general .item .switch::before{content:"";position:absolute;right:50%;top:50%;transform:translateY(-50%);width:40px;height:24px;background:#222;border-radius:15px}.general .item .switch::after{content:"";position:absolute;right:20px;top:50%;width:16px;height:16px;transform:translateY(-50%);background:#666;border-radius:50%;transition:.2s}.general .item .switch:checked::after{right:4px;background:#ff960c}.general .item button{padding:4px 16px;background:rgba(0,0,0,.2);transition:.2s}.general .item button:hover{background:rgba(0,0,0,.2666666667)}.general .item.freetime{position:relative;flex-direction:column}.general .item.freetime .top{width:100%;justify-content:space-between;display:flex}.general .item.freetime .list{width:100%;max-height:104px;border-top:1px solid rgba(255,255,255,.2666666667);list-style:none;margin-top:4px;padding:4px 0;overflow-y:auto}.general .item.freetime .list::-webkit-scrollbar{width:2px}.general .item.freetime .list::-webkit-scrollbar-thumb{background:#28b0ff}.general .item.freetime .list li{padding:2px 4px;margin:2px 0;background:rgba(0,0,0,.1333333333);text-align:center;display:flex;justify-content:space-between}.general .item.freetime .list li p{overflow-x:auto;white-space:nowrap}.general .item.freetime .list li p::-webkit-scrollbar{height:2px}.general .item.freetime .list li p::-webkit-scrollbar-thumb{background:#28b0ff}.general .item.freetime .list li.empty{color:rgba(255,255,255,.75)}.general .item.freetime .list li .delete{font-size:20px;color:rgba(255,255,255,.75);cursor:pointer;margin-left:4px}.general .item.freetime .list li .delete:hover{color:rgba(255,45,45,.9)}.general .item.freetime .addFreetime{position:absolute;left:0;top:0;width:100%;height:100%;background:#0d2d96;display:flex;align-items:center;justify-content:space-around;font-size:14px}.general .item.freetime .addFreetime .close{position:absolute;right:4px;top:4px;font-size:20px;padding:4px;transition:.2s;height:23px;display:flex;align-items:center}.general .item.freetime .addFreetime .close:hover{background:rgba(0,0,0,.1333333333)}.general .item.freetime .addingReason{position:absolute;left:0;top:0;width:100%;height:100%;padding:0 12px;background:#0d2d96;display:flex;justify-content:space-between;align-items:center}.general .item.freetime .addingReason input{width:75%;padding:4px;background:none;background:rgba(0,0,0,.1333333333);border-top-left-radius:5px;border-top-right-radius:5px;border-bottom:2px solid rgba(255,150,12,.75)}.general .item.freetime .addingReason input:focus{border-bottom:2px solid #ff960c}.general .item.freetime .addingReason .close{position:absolute;right:4px;top:4px;font-size:20px;padding:4px;transition:.2s;height:23px;display:flex;align-items:center}.general .item.freetime .addingReason .close:hover{background:rgba(0,0,0,.1333333333)}.general .timepick{position:relative;width:120px}.general .timepick .picker{width:120px;position:absolute;left:0;top:-29px;z-index:3}.general .timepick .picker .pick_items{display:flex;justify-content:space-between;align-items:center;width:100%;background:#072380}.general .timepick .picker .pick_items div{width:45%;height:84px;overflow-y:hidden;position:relative}.general .timepick .picker .pick_items div ul{list-style:none;transition:.2s}.general .timepick .picker .pick_items div ul li{background:rgba(0,0,0,.1333333333);text-align:center;height:28px;border-bottom:2px solid rgba(255,255,255,.1333333333)}.general .timepick .picker .pick_items div::before{content:"▶";position:absolute;left:1px;top:calc(50% - 1px);transform:translateY(-50%);font-size:8px;color:#ff960c}.general .timepick .picker .pick_items div::after{content:"◀";position:absolute;right:1px;top:calc(50% - 1px);transform:translateY(-50%);font-size:8px;color:#ff960c}.general .timepick .picker .pick_items .dd{transform:translateY(-3px)}.general .timepick .picker button{margin-top:2px;width:100%;background:rgba(0,0,0,.2);font-size:16px;transition:.2s}.general .timepick .picker button:hover{background:rgba(0,0,0,.2666666667);color:#28b0ff}.general .timepick .picker button:active{background:rgba(0,0,0,.4666666667)}.general .timepick .time{width:100%;text-align:center;cursor:pointer;background:rgba(0,0,0,.1333333333);border-radius:5px;padding:6px 0;margin:6px 0;transition:.2s}.general .timepick .time:hover{background:rgba(0,0,0,.2)}